
$icon-font-path: '../fonts/';

// bower:scss
// endbower

.browserupgrade {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}

.mfp-wrap {
    margin-left: 0px;
}

$narrow-page-side-margin: 260px;
.page-main.narrow-side {
    margin-left: $narrow-page-side-margin;
}

.page-side.narrow {
    width: $narrow-page-side-margin;
}

.portfolio-nav.no-ajax {
    display: inherit;
}

#blog-more.disabled,
.portfolio-nav .disabled {
    pointer-events: none;
    opacity: 0.2;
}

.tj-circle-hover .tj-overlay {
    opacity: 0.6;
}

.vertical-carousel .item.black-bg {
    background-color: #000;
}

@if $use-overlay-pattern {
    .kb-slider:after {
        background: url('../images/overlay-pattern2.png') repeat;
    }
}

.no-capital {
    text-transform: inherit !important;
}

.btn-toranj-blog {
    color: $light-main !important;
    border-color: $light-main;
}

#navigation  li.current-menu-item  a:before,
#navigation  li.current-menu-parent  >a:before,
#navigation  .nav-prev>a:before {
    width: 6px;
}

#navigation > li > a:hover,
#navigation > li > a:focus,
#navigation > li.current-menu-item > a,
#navigation > li.current-menu-parent > a {
    border-left: 4px solid #505050;
}

#navigation li ul li a:hover,
#navigation li ul li a:focus,
#navigation li ul li.current-menu-item a {
    border-left: 1px solid #505050;
}

.blog-minimal #blog-list .post-thumb:after {
    content: "\f064";
}

#loader{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:#1c1c1c;
    z-index: 1000;
}

#loader #followingBallsG,
body.loading #followingBallsG{
    display: block;
}

.show-sidebar #loader{
    left:270px;
}

/* CSS for devices with normal screens */
#main-logo {
    background-image: url(../images/logo.png);
    background-repeat: no-repeat;
    width: 190px;
    height: 190px;
}

#main-logo-mini {
    background-image: url(../images/logo-mini.png);
    background-repeat: no-repeat;
    width: 40px;
    height: 276px;
}

.grid-filters li a.filter-others {
    text-transform: none;
}

/* CSS for high-resolution devices
**************************************************/
/* 1.25 dpr */
@media
(-webkit-min-device-pixel-ratio: 1.25),
(min-resolution: 120dpi) {
    #main-logo {
        background-image: url(../images/logo_x1-25.png);
        background-size: 190px 190px;
        background-repeat: no-repeat;
        width: 190px;
        height: 190px;
    }

    #main-logo-mini {
        background-image: url(../images/logo-mini_x1-25.png);
        background-size: 40px 40px;
        background-repeat: no-repeat;
        width: 40px;
        height: 40px;
    }
}

/* 1.3 dpr */
@media
(-webkit-min-device-pixel-ratio: 1.3),
(min-resolution: 124.8dpi) {
    #main-logo {
        background-image: url(../images/logo_x1-3.png);
        background-size: 190px 190px;
        background-repeat: no-repeat;
        width: 190px;
        height: 190px;
    }

    #main-logo-mini {
        background-image: url(../images/logo-mini_x1-3.png);
        background-size: 40px 276px;
        background-repeat: no-repeat;
        width: 40px;
        height: 276px;
    }
}

/* 1.5 dpr */
@media
(-webkit-min-device-pixel-ratio: 1.5),
(min-resolution: 144dpi) {
    #main-logo {
        background-image: url(../images/logo_x1-5.png);
        background-size: 190px 190px;
        background-repeat: no-repeat;
        width: 190px;
        height: 190px;
    }

    #main-logo-mini {
        background-image: url(../images/logo-mini_x1-5.png);
        background-size: 40px 276px;
        background-repeat: no-repeat;
        width: 40px;
        height: 276px;
    }
}

/* 2 dpr Retina */
@media
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi) {
    #main-logo {
        background-image: url(../images/logo_x2.png);
        background-size: 190px 190px;
        background-repeat: no-repeat;
        width: 190px;
        height: 190px;
    }

    #main-logo-mini {
        background-image: url(../images/logo-mini_x2.png);
        background-size: 40px 276px;
        background-repeat: no-repeat;
        width: 40px;
        height: 276px;
    }
}

/* Responsiveness
**************************************************/
@media only screen and (max-width: 992px){
    .page-main.narrow-side {
        margin-left: 0;
    }
}

@media (max-width: 499px) {
    .hidden-xxs {
        display: none !important;
    }

    .disable-href {
        pointer-events: none;
    }
}

@media (min-width: 500px) and (max-width: 767px) {
    .hidden-xxs-sm {
        display: none !important;
    }
}

// scroll on about page
.vertical-carousel .left-side {
    overflow: auto !important;
}

#post-body #post-side .post-meta .post-author-image picture > img,
.blog-minimal #blog-list .post-thumb picture > img {
    width: 100%;
    height: auto;
    border-radius: 50%;
}

.vertical-carousel .right-side-wrapper .item picture > img {
    height: 100%;
    width: auto;
    position: absolute;
    top: 0;
    left: 0;
}

.tj-mp-action i {
    border: 1px solid $light-main;
}
.tj-mp-action:hover i,
.tj-mp-action:focus i,
.tj-mp-close:hover i,
.tj-mp-close:focus i{
    background-color: $light-main;
    color:#272822;
}

body {
    overscroll-behavior-x: contain;
}
