@media (prefers-color-scheme: dark) {
    #side-bar,
    .sub-menu {
        background: $dark-sub-menu;
    }
    #navigation li.current-menu-item a:before, #navigation li.current-menu-parent > a:before, #navigation .nav-prev > a:before {
        background: $dark-main;
    }
    #navigation li {
        background: $dark-nav-bg;
        border-top-color: $dark-border;
        border-bottom-color: $dark-border;
    }
    #navigation li:first-child{
        border-top-color: $dark-border;
    }
    #navigation li a {
        box-shadow: inset -4px 0px 6px -4px $dark-nav-link;
    }
    #navigation li a:hover,
    #navigation li a:focus,
    #navigation li.current-menu-item a,
    #navigation li.current-menu-parent > a {
        color: $dark-nav-current-link;
        background: $dark-nav-current-link-bg;
    }

    #navigation > li > a:hover,
    #navigation > li > a:focus,
    #navigation > li.current-menu-item > a,
    #navigation > li.current-menu-parent > a {
        border-left: 4px solid $dark-main; //#505050
    }

    #navigation li ul li a:hover,
    #navigation li ul li a:focus,
    #navigation li ul li.current-menu-item a {
        border-left: 1px solid $dark-main; //#505050
    }

    #navigation ul.sub-menu li {
        border-bottom: none;
        border-top: none;
    }
    #navigation ul.sub-menu li.nav-prev a {
        background: $dark-nav-current-link-bg;
        color: $dark-nav-current-link;
    }
    #copyright:after {
        background-color: $dark-nav-copyright;
        box-shadow: 0 1px 0 0 $dark-nav-copyright-shadow;
    }

    #inner-bar {
        background: $dark-form-bg;
    }

    /**/
    #main-content {
        color: $dark-main-content-text;
        background-color: $dark-main-content-bg;
    }
    .blog-list{
        background-color: $dark-blog-list-bg;
    }
    h1,h2,h3,h4,h5,h6{
        color: $dark-heading-text;
    }
    strong,em{
        color: $dark-strong-text;
    }

    .grid-portfolio .gp-item a:after{
        border-color: $dark-main-content-bg;
    }

    /**/
    .regular-page h1,h2,h3,h4,h5,h6 {
        color:$dark-heading-text !important;
    }
    .regular-page {
        color: inherit;
    }

    hr {
        border-top-color: $dark-border;
    }

    /* Form elements
**************************************************/
    input,textarea,.form-control{
        border: 1px solid $dark-border;//#F2F2F2;
        background-color: $dark-form-bg;
        color: $dark-heading-text;
    }

    .form-control:focus{
        border-color: $dark-border;//#E8E8E8;
        box-shadow: 0 0 1px rgba(102, 175, 233, 0.2);
        -webkit-box-shadow :0 0 1px rgba(102, 175, 233, 0.2);
    }

    .tj-form input:not([type="submit"]),.tj-form textarea {
        border: 1px solid $dark-input-textarea;
        box-shadow: 0 0 1px 0 #36382F;
        background-color: $dark-border;
        color: $dark-heading-text;
    }
    .tj-form input:not([type="submit"]):focus,.tj-form textarea:focus{
        background-color:$dark-border;
        border: 1px solid $dark-input-textarea-focus;
        box-shadow: 0 0 1px 0 #534741;

    }


    /* Accordion
**************************************************/
    .accordion .head {
        background: $dark-form-bg;//#fafafa;
        border: 1px solid $dark-border; //#ebebeb;
    }
    .accordion .item:hover .head,
    .accordion .item:focus .head {
        background: $dark-form-bg;//#f3f3f3;
    }
    .accordion .item .head a{
        color: $dark-heading-text;//#474747;
    }
    .accordion .item .body{
        background: $dark-form-bg;//#fdfdfd;
        border: 1px solid $dark-border;//#ebebeb;
        box-shadow: inset 0px 1px 6px -4px rgba(0,0,0,0.2);
    }

    /* Call to action
**************************************************/
    .call-to-action{
        background: $dark-form-bg; //#fafafa;
        border-color: $dark-border;//#ebebeb;
    }
    .call-to-action .action-title{
        color: #474747;
    }


    /**/
    #post-body,
    #post-content {
        border-bottom-color:$dark-border;
    }

    /* CSS for devices with normal screens */
    #main-logo {
        background-image: url(../images/logo-dark.png);
    }

    #main-logo-mini {
        background-image: url(../images/logo-mini.png);
    }

    /* CSS for high-resolution devices
    **************************************************/
    /* 1.25 dpr */
    @media
    (-webkit-min-device-pixel-ratio: 1.25),
    (min-resolution: 120dpi) {
        #main-logo {
            background-image: url(../images/logo-dark_x1-25.png);
        }

        #main-logo-mini {
            background-image: url(../images/logo-dark-mini_x1-25.png);
        }
    }

    /* 1.3 dpr */
    @media
    (-webkit-min-device-pixel-ratio: 1.3),
    (min-resolution: 124.8dpi) {
        #main-logo {
            background-image: url(../images/logo-dark_x1-3.png);
        }

        #main-logo-mini {
            background-image: url(../images/logo-dark-mini_x1-3.png);
        }
    }

    /* 1.5 dpr */
    @media
    (-webkit-min-device-pixel-ratio: 1.5),
    (min-resolution: 144dpi) {
        #main-logo {
            background-image: url(../images/logo-dark_x1-5.png);
        }

        #main-logo-mini {
            background-image: url(../images/logo-dark-mini_x1-5.png);
        }
    }

    /* 2 dpr Retina */
    @media
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi) {
        #main-logo {
            background-image: url(../images/logo-dark_x2.png);
        }

        #main-logo-mini {
            background-image: url(../images/logo-dark-mini_x2.png);
        }
    }
}
